.title-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #333;
  color: #fff;
}

.game-title {
  font-size: 3em;
  margin-bottom: 50px;
}

.start-button {
  padding: 15px 30px;
  font-size: 1.5em;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.start-button:hover {
  background-color: #0056b3;
}
